import axios from "axios";
import React from "react";
import "./form.css";

const api="https://jsonplaceholder.typicode.com/posts";

class Posts extends React.Component{
    constructor(props){
        super(props);
        this.state={
            posts:[],
            id:"",
            userId:"",
            postId:"",
            title:"",
            body:"",
        };
    }

createPost= async () =>{
    try{
        const {userId,title,body} =this.state;
        const {data:post} = await axios.post(api,{userId,title,body});
        let posts =[...this.state.posts];
        posts.push(post);
        this.setState({posts,userId:"",title:"",body:""});
        console.log(posts);
    }catch(err){
        console.log(err,"Creating error");
    }
}; 

  
readPost = async()=>{
    try{
        const {data} = await axios.get(api);
        this.setState({posts:data});
        console.log(data);
    }catch(err){
        console.log(err,"Fetching error");
    }

};


updatePost= async()=>{
    try{
        const {id,userId,title,body} =this.state;
        const {data:post} = await axios.put(`${api}/${id}`,{userId,title,body});
        let posts= [...this.state.posts];
        const index = posts.findIndex((post)=>post.id === id);
        posts[index]=post;
        this.setState({posts, userId:"",title:"",body:""});
    }catch(err){
        console.log(err,"updating error");
    }
};


deletePost= async(postId)=>{
    try{
        await axios.delete(`https://jsonplaceholder.typicode.com/posts/${postId}`);
        let posts =[...this.state.posts];
        posts=posts.filter((post)=>post.id!==postId);
        this.setState({posts}) ;       
        console.log(`${postId}`, "Deleted");
    }catch(err){
        console.log(err,"Deleting error");
    }

};

componentDidMount(){
    this.readPost();
}

handleChange = ({target:{name,value}})=> {
    this.setState({ [name]:value });
};

handleSubmit =(event)=>{
    event.preventDefault();
    if(this.state.id){
        this.updatePost();
    }
    else{
    this.createPost();
    }    
};

setUpdate = (post)=>{
    this.setState({...post});
}

render(){
    return(
       <>
        <form align="center" onSubmit={this.handleSubmit}>
        <div className="form">
           <img className="img" src="https://thumbs.dreamstime.com/b/user-avatar-icon-button-profile-symbol-flat-person-icon-vector-user-avatar-icon-button-profile-symbol-flat-person-icon-%C3%A2%E2%82%AC-stock-131363829.jpg" />
           <p><label className="label">UserId</label><input className="text" name="userId" type="text" value={this.state.userId} onChange={this.handleChange} required/><br/></p>
           <p><label className="label">Title</label><input className="text" name="title" type="text" value={this.state.title} onChange={this.handleChange} required/><br/></p>
           <p><label className="label">Body</label><input className="text" name="body" type="text" value={this.state.body} onChange={this.handleChange} required/><br/></p>
           <p><input name="Submit" type="submit"></input></p>
        </div>
       </form>
        <table>
            <tbody>
            <tr>
                <th>PostId</th>
                <th>Id</th>
                <th>Title</th>
                <th>Body</th>
                <th>Actions</th>
            </tr>
            {this.state.posts.map((post)=>{
            return (            
            <tr>
                <td>{post.id}</td>
                <td>{post.userId}</td>
                <td>{post.title}</td>
                <td>{post.body}</td>
                <td>
                    <button onClick={()=>this.setUpdate(post)}>Update</button>
                    <button onClick={()=>this.deletePost(post.id)}>Delete</button>
                </td>
            </tr>
            );
            })}
            </tbody>
        </table>
        </>
    );
}
}

export default Posts;