import React from "react";
import {NavLink} from "react-router-dom";
import { Navbar,Nav, Container } from "react-bootstrap";

import "./Nav.css"
import { green } from "@mui/material/colors";


class Navigation extends React.Component{
    render(){
    return(
        <Navbar collapseOnSelect fixed="top" bg="dark" expand="lg" variant="dark">
          <Container>
          <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
          <Navbar.Collapse id="responsive-navbar-nav">
          <Nav>
            <NavLink exact activeStyle={{color:"green"}} to="/">Home</NavLink>
            <NavLink activeStyle={{color:"green"}} to="/user">User</NavLink>
            <NavLink activeStyle={{color:"green"}} to="/posts">Posts</NavLink>
            <NavLink activeStyle={{color:"green"}} to="/comments">Comments</NavLink>
          </Nav>
          </Navbar.Collapse>
          </Container>
        </Navbar>
    )
    }
}

export default Navigation;