import React from "react";
import axios from "axios";
import "./form.css";

const userApi="https://jsonplaceholder.typicode.com/users";


class User extends React.Component{
    constructor(props){
        super(props);
        this.state={
            users:[],
            id:"",
            name:"",
            username:"",
            email:"",
            address: [],
            phone:"",
            website:"",
            company:[]
        };
    }


    createUser= async () =>{
        try{
            const {id,name,username} =this.state;
            const {data:user} = await axios.post(userApi,{id,name,username});
            let users =[...this.state.users];
            users.push(user);
            this.setState({users,id:"",name:"",username:""});
            console.log(users);
        }catch(err){
            console.log(err,"Creating error");
        }
    }; 
    
      
    readUser = async()=>{
        try{
            const {data} = await axios.get(userApi);
            this.setState({users:data});
            console.log(data);
        }catch(err){
            console.log(err,"Fetching error");
        }
    
    };
    
    
    updateUser= async()=>{
        try{
            const {id,name,username,email,address,phone,website,company} =this.state;
            const {data:user} = await axios.put(`${userApi}/${id}`,{id,name,username,email,address,phone,website,company});
            let users= [...this.state.users];
            const index = users.findIndex((user)=>user.id === id);
            users[index]=user;
            this.setState({users, id:"",name:"",username:"",email:"",address:"",phone:"",website:"",company:""});
        }catch(err){
            console.log(err,"updating error");
        }
    };
    
    
    deleteUser= async(id)=>{
        try{
            await axios.delete(`https://jsonplaceholder.typicode.com/users/${id}`);
            let users =[...this.state.users];
            users=users.filter((user)=>user.id!==id);
            this.setState({users}) ;       
            console.log(`${id}`, "Deleted");
        }catch(err){
            console.log(err,"Deleting error");
        }
    
    };
    
    componentDidMount(){
        this.readUser();
    }

    handleChange = ({target:{name,value}})=> {
        this.setState({ [name]:value });
    };
    
    handleSubmit =(event)=>{
        event.preventDefault();
        if(this.state.id){
            this.updateUser();
        }
        else{
        this.createUser();
        }    
    };
    
    setUpdate = (user)=>{
        this.setState({...user});
    }
    
    render(){
        return(
           <>
           <form align="center" onSubmit={this.handleSubmit}>
           <div className="form">
           <img className="img" src="https://thumbs.dreamstime.com/b/user-avatar-icon-button-profile-symbol-flat-person-icon-vector-user-avatar-icon-button-profile-symbol-flat-person-icon-%C3%A2%E2%82%AC-stock-131363829.jpg" />
               <p><label className="label">Id</label><input className="text" name="id" type="text" value={this.state.id} onChange={this.handleChange} required/><br/></p>
               <p><label className="label">Name</label><input className="text" name="name" type="text" value={this.state.name} onChange={this.handleChange} required/><br/></p>
               <p><label className="label">UserName</label><input className="text" name="username" type="text" value={this.state.username} onChange={this.handleChange} required/><br/></p>
               <p><label className="label">Email</label><input className="text" name="email" type="text" value={this.state.email} onChange={this.handleChange} required/><br/></p>
               <p><label className="label">Address</label><input className="text" name="address" type="text" value={this.state.address} onChange={this.handleChange} required/><br/></p>
               <p><label className="label">Phone</label><input className="text" name="phone" type="text" value={this.state.phone} onChange={this.handleChange} required/><br/></p>
               <p><label className="label">Website</label><input className="text" name="website" type="text" value={this.state.website} onChange={this.handleChange} required/><br/></p>
               <p><label className="label">Company</label><input className="text" name="company" type="text" value={this.state.company} onChange={this.handleChange} required/><br/></p>
               <p><input name="Submit" type="submit"></input></p>
            </div>
           </form>
            <table>
                <tbody>
                <tr>
                    <th>Id</th>
                    <th>Name</th>
                    <th>UserName</th>
                    <th>Email</th>
                    <th>Address
                    <th colSpan="5"  rowSpan="2">Street:</th>
                    <th>Suite:</th>
                    <th>City:</th>
                    <th>Zipcode:</th>
                    <th>Geo:
                    <th colSpan="2" rowSpan="2">lat:</th>
                    <th>lng:</th>
                    </th>
                </th>
                    <th>Phone</th>
                    <th>Email</th>
                    <th>Address</th>
                    <th>Phone</th>
                    <th>Website</th>
                    <th>Company
                        <th colSpan="3" rowSpan="2">Name:</th>
                        <th>CatchPhrase:</th>
                        <th>BS:</th>
                    </th>
                    <th>Actions</th>
                </tr>
                {this.state.users.map((user)=>{
                return (            
                <tr>
                    <td>{user.id}</td>
                    <td>{user.name}</td>
                    <td>{user.username}</td>
                    <td>{user.email}</td>
                    <td>
                    <td>{user.address.street}</td>
                    <td>{user.address.suite}</td>
                    <td>{user.address.city}</td>
                    <td>{user.address.zipcode}</td>
                    <td>
                        <td>{user.address.geo.lat}</td>
                        <td>{user.address.geo.lng}</td>
                    </td>
                    </td>
                    <td>{user.phone}</td>
                    <td>{user.website}</td>
                    <td>
                    <td>{user.company.name}</td>
                    <td>{user.company.catchPhrase}</td>
                    <td>{user.company.bs}</td>
                    </td>
                    <td>
                        <button onClick={()=>this.setUpdate(user)}>Update</button>
                        <button onClick={()=>this.deletePost(user.id)}>Delete</button>
                    </td>
                </tr>
                );
                })}
                </tbody>
            </table>
            </>
        );
    }

}
export default User;